<template>
  <div class="q-gutter-sm">
    <q-list>
      <book-side-bar-item :item="rootItem"></book-side-bar-item>
    </q-list>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed } from 'vue';
import { DocTree } from '../../shared/docTree';
import BookSideBarItem from './BookSideBarItem.vue';
import { ItemInfo } from './BookSideBarItem.vue';

export default defineComponent({
  name: 'BookSideBar',
  props: {
    docTree: {
      type: DocTree,
      default: new DocTree()
    },
    basePath: {
      type: String,
      default: ''
    }
  },
  compoent: {
    BookSideBarItem
  },
  setup(props) {
    const rootItem = computed(() => new ItemInfo(props.docTree.root, 0, props.basePath));
    return { rootItem };
  }
});
</script>
